import React, { Fragment } from 'react';
import Title from '../Title';
import { BrowserRouter as Router, Link, Switch, Route, useParams, useRouteMatch, useLocation } from 'react-router-dom';
import Navbar from 'react-bootstrap/Navbar';
import Nav from 'react-bootstrap/Nav';
import logo from '../../logo.svg';
import AppCss from './AppCss.module.css';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Content from '../Content';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPhone, faMapMarker, faEnvelope } from '@fortawesome/free-solid-svg-icons';
import classname from 'classnames';

function useQuery() {
    return new URLSearchParams(useLocation().search)
}

const UserInformation = () => {
    const { userId } = useParams();
    const QueryParams = useQuery();
    const query = [...QueryParams.entries()];
    return (
        <div>
            <Content>Users</Content>
            <div className="my-4">
                {
                    userId === undefined
                        ? query.map((val, index) =>
                            <li key={index}> {val[0]} : {val[1]}  </li>
                        )
                        : "User Id is " + userId
                }
            </div>
        </div>
    )
}

const CourseDetails = () => {
    const { path, url } = useRouteMatch();
    return (
        <div>
            <Link to={`${url}/react-js`}>React JS</Link><br />
            <Link to={`${url}/node-js`}>Node JS</Link><br />
            <Link to={`${url}/angular`}>Angular</Link><br />
            <br /><br /> 
            <Switch>
                <Route path={`${path}/react-js`}>React JS</Route>
                <Route path={`${path}/node-js`}>Node js</Route>
                <Route path={`${path}/angular`}>Angular</Route>
                <Route path={`${path}`}>Please select any Js course</Route>
            </Switch>
        </div>
    )
}

const Courses = () => {
    const { path, url } = useRouteMatch();
    return (
        <div className={classname('my-2')}>
            <Link to={`${url}/jsCourses`} >JS Courses</Link><br />
            <Link to={`${url}/other`} >Other Courses</Link><br /> <br />
            <Switch >
                <Route path={`${path}/other`}>Other Courses</Route>
                <Route path={`${path}/:jsCourses`}><CourseDetails /></Route>
                <Route path={`${path}`}>Please select the course</Route>
            </Switch>
        </div>
    )
}

const App = () => {
    return (
        <Fragment>
            <Title />
            <Router basename="/aneriPatel" forceRefresh={false}>
                <Navbar collapseOnSelect expand="md" bg="dark" variant="dark">
                    <Navbar.Brand className="mx-5">
                        <img src={logo} width="30px" height="30px" className="d-inline-block align-top" alt="React" />
                    </Navbar.Brand>
                    <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                    <Navbar.Collapse id="responsive-navbar-nav">
                        <Nav className={AppCss.navbarLink}>
                            <Link to="/">Home</Link>
                            <Link to="/users"> Users</Link>
                            <Link to="/courses"> Courses</Link>
                            <Link to="/help"> Help</Link>
                            <Link to="/about-us">About us</Link>
                            <Link to="/contact-us">Contact Us</Link>
                        </Nav>
                    </Navbar.Collapse>
                </Navbar>

                <Container>
                    <div className={AppCss.ContentSection}>
                        <Switch>
                            <Route path='/users/:userId?'><UserInformation /></Route>
                            <Route path="/courses"><Content>Courses</Content><Courses /></Route>
                            <Route path="/help"><Content>Help</Content></Route>
                            <Route path="/about-us"><Content>About us</Content></Route>
                            <Route path="/contact-us"><Content>Contact us</Content></Route>
                            <Route path="/faq"><Content>Frequently Asked Questions</Content></Route>
                            <Route path="/privacy-policy"><Content>Privacy Policy</Content></Route>
                            <Route path="/resources"><Content>Resources</Content></Route>
                            <Route exact path="/"><Content>Home</Content></Route>
                            <Route exact path="*">Page Not Found</Route>
                        </Switch>
                    </div>
                </Container>

                <footer className={AppCss.Footer}>
                    <Container fluid>
                        <Row>
                            <Col md="6" className={AppCss.FooterContactDetails}>
                                <h5 className="pt-3 pb-2">Contact Details</h5>
                                <Row>
                                    <Col xs="1">
                                        <FontAwesomeIcon icon={faMapMarker} />
                                    </Col>
                                    <Col>
                                        <small><b>Address</b></small><br />
                                        <small>123, Street , Seattle, WA 98112.</small>
                                    </Col>
                                </Row>
                                <Row>
                                    <Col xs="1">
                                        <FontAwesomeIcon icon={faPhone} />
                                    </Col>
                                    <Col>
                                        <small><b>Call Us</b></small><br />
                                        <small>+123-456-789</small>
                                    </Col>
                                </Row>
                                <Row>
                                    <Col xs="1">
                                        <FontAwesomeIcon icon={faEnvelope} />
                                    </Col>
                                    <Col>
                                        <small><b>Email</b></small><br />
                                        <small>info@companyname.com</small>
                                    </Col>
                                </Row>
                            </Col>

                            <Col md="6" className={AppCss.footerLink}>
                                <h5 className="pt-3 pb-2">Useful Links</h5>
                                <p><small><Link to="/faq">Frequently Asked Questions</Link></small></p>
                                <p><small><Link to="/privacy-policy">Privacy Policy</Link></small></p>
                                <p><small><Link to="/resources">Resources</Link></small></p>
                            </Col>
                        </Row>
                        <hr />
                        <Row>
                            <Col>
                                <div className="text-center">
                                    <small > &copy; {new Date().getFullYear()} Copyright : All rights reserved</small>
                                </div>
                            </Col>
                        </Row>
                    </Container>
                </footer>
            </Router>
        </Fragment >
    );
}

export default App;